import React from 'react';
import { lighten, makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Button from '@material-ui/core/Button';
import {Contracts, Formatter, Accounts} from '../../../../utils/index';
import TextField from '@material-ui/core/TextField';

const useStyles = makeStyles(theme => ({
  root: {
    backgroundColor: lighten(theme.palette.background.paper, 0.1),
  },
  table: {
    minWidth: 650,
  },
  liqSlip: {
    marginTop: '10px'
  }
}));

function createData(token, liquidityUsed, amount, currentValue, tokenSlippage, tokenAddress, id) {
  return { token, liquidityUsed, amount, currentValue, tokenSlippage, tokenAddress, id };
}

async function liquidateToken(index, address) {
    let path = [address, '0xbb4CdB9CBd36B01bD1cBaEBF2De08d9173bc095c'];
    let deadline = Date.now() + 60 * 20;
    let slippage = 0;
    if (document.getElementById('liq-slippage'+index).value > 0) {
      slippage = document.getElementById('liq-slippage'+index).value*100;
    }

    let res = await Contracts.liquidateToken(true, index, path, deadline, slippage);
}

export default function SimpleTable(res) {
  let rows = [

  ];
  const classes = useStyles();
  let data = res['poolData'];
  for (let i = 0; i < data[0].length; i++) {
      rows.push(createData(data[5][i], data[1][i], data[2][i], data[3][i], data[4][i], data[0][i], i));
  }  
  return (
    <TableContainer component={Paper} className={classes.root}>
      <Table className={classes.table} aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell>Token</TableCell>
            <TableCell align="left">Liquidity Used</TableCell>
            <TableCell align="left">Amount</TableCell>
            <TableCell align="left">Current Value</TableCell>
            
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map(row => (
            <TableRow key={row.token}>
            <TableCell align="left">{row.token}</TableCell>
            <TableCell align="left" component="th" scope="row">
                {Formatter.getRoundedBalance(row.liquidityUsed)}
            </TableCell>
            <TableCell align="left">{Formatter.getRoundedBalance(row.amount)}</TableCell>
            <TableCell align="left">{Formatter.getRoundedBalance(row.currentValue)}</TableCell>
            <TableCell align="left">
                <Button onClick={() => liquidateToken(row.id, row.tokenAddress)} variant="contained" color="primary">
                    Liquidate
                </Button>
                <TextField className={classes.liqSlip} id={'liq-slippage'+row.id} type="number" label="Slippage" variant="outlined" />
            </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}
