import React, { useState } from 'react';
import { lighten, makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import Paper from '@material-ui/core/Paper';
import { Box } from '@material-ui/core';
import {Contracts, Formatter, Accounts} from '../../../utils/index';
import { useEffect } from 'reactn';
import Investments from './Investments';


const useStyles = makeStyles(theme => ({
  root: {
    width: '60%',
    minWidth: '320px',
    height: '80%',
    maxHeight: '480px',
    margin: '0 auto',
    backgroundColor: lighten(theme.palette.background.paper, 0.1),
  },
  rootBox: {
    display: 'flex',
    '& > *': {
      backgroundColor: lighten(theme.palette.background.paper, 0.1),
      margin: theme.spacing(2),
      width: theme.spacing(32),
      height: theme.spacing(32),
    },
    
  },
  paperStyle: {
    width: '100%',
    height: '100px',
    display: 'block',
    borderRadius: '10px',
    position: 'relative'
  },
  bullet: {
    display: 'inline-block',
    margin: '0 2px',
    transform: 'scale(0.8)',
  },
  title: {
    fontSize: 14,
  },
  pos: {
    marginBottom: 12,
  },
  symbol: {
    height: '30px',
    right: '10px',
    bottom: '12px',
    position: 'absolute'
  },
  bnbLogo: {
    height: '30px',
    right: '10px',
    bottom: '12px',
    position: 'absolute'
  },
  bnbText: {
    display: 'inline-block',
    bottom: '8px',
    marginRight: '10px',
    position: 'relative'
  },
  fromLogo: {
    height: '100%'
  },
  leadText: {
      position: 'absolute',
      top: '10px',
      left: '8px'
  },
  numInput: {
      position: 'absolute',
      bottom: '14px',
      left: '8px',
      width: '65%'
  },
  approveSpend: {
      width: '80%',
      height: '40px',
      margin: '0 auto',
      marginTop: '15px',
      display: 'block'
  },
  slippage: {
      marginLeft: '8px'
  },
  poolAddressInput: {
      position: 'absolute',
      marginTop: '20px',
      marginLeft: '10px',
      width: '300px'
  },
  poolAddress: {
      display: 'inline-block',
      marginBottom: '20px',
      marginTop: '40px'
  }
}));

function determineNetwork() {
    if (window.localStorage.getItem('chain') == 'ETH') {
      return ' ETH'
    } else {
        return ' BNB'
    }
}



export default function SimpleCard() {
  const classes = useStyles();
  const [name, setName] = React.useState('name');
  const [holdings, setHoldings] = React.useState('0');
  const [isSelfManager, setIsSelfManager] = React.useState(true);
  const [swapPool, setSwapPool] = React.useState('');
  const [investments, setInvestments] = React.useState({0:[], 1:[], 2:[], 3:[], 4:[]});

  async function getName() {
    let token = document.getElementById('from-address').value;
    let name = await Contracts.getTokenName(token);
    setName(name);
  }

  useEffect(() => {
    async function loadData() {
        let holdingsData = await Contracts.getManagerHoldings();
        setHoldings(holdingsData['holdings']);
        setIsSelfManager(holdingsData['isSelfManager']);
        setInvestments(await Contracts.getManagerInvestments(await Accounts.getCurrentAccount()));
    }       

    loadData();
  }, []);

  async function performSwap() {
    let amount = document.getElementById('bnb-amount').value;
    let path = ['0xbb4CdB9CBd36B01bD1cBaEBF2De08d9173bc095c', document.getElementById('from-address').value]
    let slippage = document.getElementById('slippage').value*100;
    let deadline = Date.now() + 60 * 20;
    let res = await Contracts.swapToken(isSelfManager, amount, path, slippage, deadline);
  }

  async function updateInvestments() {
    let account = document.getElementById('inv-input').value;
    console.log(account);  
    setInvestments(await Contracts.getManagerInvestments(account));
  }

  return (
    <div>
        <Card className={classes.root}>
            <CardContent>
                <Typography className={classes.title} color="textSecondary" gutterBottom>
                    My {determineNetwork()} {holdings}
                </Typography>
                
                <Box className={classes.rootBox}>
                    <Paper variant="outlined" className={classes.paperStyle}>
                        <Typography className={classes.leadText} color="textPrimary">
                            From
                        </Typography>
                        <TextField id="bnb-amount" label="Enter Amount" className={classes.numInput} />
                        <div className={classes.bnbLogo}>
                            <Typography className={classes.bnbText} >
                                {determineNetwork()}
                            </Typography>
                            {determineNetwork() == ' ETH' ?
                                <img className={classes.fromLogo} src="/images/eth.png"></img>
                                :
                                <img className={classes.fromLogo} src="/images/bnb.png"></img>
                            }
                        </div>
                    </Paper>
                </Box>

                <Box className={classes.rootBox}>
                    <Paper variant="outlined" className={classes.paperStyle}>
                        <Typography className={classes.leadText} color="textPrimary">
                            To
                        </Typography>
                        <TextField onChange={() => getName()} id="from-address" label="Enter Address" className={classes.numInput} />
                        <Button className={classes.symbol} variant="outlined">{name}</Button>
                    </Paper>
                </Box>

                <TextField id="slippage" className={classes.slippage} label="Set Slippage %" type="number" variant="outlined" />
                <Button onClick={() => performSwap()} className={classes.approveSpend} variant="contained" color="primary">
                    Swap
                </Button>
            </CardContent>
            <CardActions>

            </CardActions>
        </Card>
        <Typography className={classes.poolAddress} variant="h1" component="h2" gutterBottom>
            Pool Investments: 
        </Typography>
        <TextField onClick={() => updateInvestments()} className={classes.poolAddressInput} id="inv-input" label="Enter Pool Address" />
        <Investments poolData={investments} />
    </div>
    
  );
}
